html {
  height: 100%;
  background-color: #f2f2f2;
}
body {
  padding: 0;
  margin: 0;
}
img {
  display: block;
}
ul,
ol,
li {
  padding: 0;
  margin: 0;
}
li {
  list-style-type: none;
  display: block;
}
a {
  text-decoration: none;
  display: block;
}
fieldset {
  border: none;
  padding: 0;
  margin: 0;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/ptsans.woff") format("woff"), url("../../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../../fonts/ptsansbold.woff") format("woff"), url("../../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.body-container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  max-width: 1200px;
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  text-align: center;
  align-items: center;
  background-color: #ffffff;
  margin: 0 auto;
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .body-container {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
}
.main-header {
