.feedback__header {
  padding-top: 37px;
  padding-bottom: 27px;
  padding-left: 35px;
  padding-right: 35px;
  .border-page;

  @media (min-width: @tablet-width) {
    padding-top: 62px;
    padding-left: 165px;
    padding-right: 166px;
    border: none;
    margin-bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 114px;
    padding-left: 378px;
    padding-right: 379px;
    margin-bottom: 14px;
  }
}

.feedback__title {
  margin: 0;
  margin-bottom: 24px;
  .font-family-bold;
  font-size: 18px;
  line-height: 26px;
  color: #000000;

  @media (min-width: @tablet-width) {
    padding-top: 2px;
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 26px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 32px;
  }
}
